import {get} from '@/api/homepage.js'
export default {
	components:{
		
	},
	data() {
		return {
			benefit: {},
		}
	},
	onLoad() {
		this.id = this.$root.$mp.query.id
		this.getBenefitByid()
	},
	/*
	*
	* 获取主页传参对应的优惠券详细信息
	*/ 
	methods: {
		async getBenefitByid() {
			let _this = this 
			const res = await get('/api/benefitbyid',{
				benefit_id : _this.id
			})
			_this.benefit = res.data[0]
			console.log(_this.benefit)
		}

	},
}
